import React, { useState, useEffect } from 'react';

const OrderBook = () => {
  const [orders, setOrders] = useState([]);
  const currencyPair = 'btcusd';

  const currencyArray = currencyPair.toUpperCase().match(/.{1,3}/g);

  useEffect(() => {
    const subscribe = {
      event: 'bts:subscribe',
      data: {
        channel: `order_book_${currencyPair}`
      }
    }; 
    const ws = new WebSocket('wss://ws.bitstamp.net');

    ws.onopen = () => {
      ws.send(JSON.stringify(subscribe));
    };
    ws.onmessage = (event) => {
      const response = JSON.parse(event.data);
      setOrders(response.data);
    };
    ws.onclose = () => {
      ws.close();
    };

    return () => {
      ws.close();
    };
  }, [currencyPair]);

  const { bids, asks } = orders;
  const orderRows = (arr) =>
    arr &&
    arr.map((item, index) => (
      <tr key={index} className='orderbook-text'>
        <td className='orderbook-text-buy'> {item[1]} </td>
        <td className='orderbook-text-buy'> {item[0]} </td>
        <td className='orderbook-text-buy orderbook-total'> 54.00 </td>
      </tr>
    ));
  const orderHead = (title) => (
    <thead>
      <tr>
        <th colSpan="2">{title}</th>
      </tr>
      <tr className='orderbook-text'>
        <th> ({currencyArray[0]})</th>
        <th> ({currencyArray[1]})</th>
      </tr>
    </thead>
  );
  return (
    <div className="order-container">
      <table>
        <tbody>{orderRows(bids)}</tbody>
      </table>
    </div>
  );
};

export default OrderBook;
