import React from 'react';


export default class News extends React.Component {
  state = {
    todos: []
  }

  componentDidMount() {
    fetch('https://api.binance.com/api/v3/trades?symbol=ETHBTC')
    .then(res => res.json())
    .then((data) => {
      this.setState({ todos: data.symbols })
      console.log(this.state.todos)
    })
    .catch(console.log)
  }

  render() {

    return (
       <div className="container">
        <div className="col-xs-12">
        <h1>My Todos</h1>
        {this.state.todos.map((todo) => (
          <div className="card">
            <div className="card-body">
              <h5 className="card-subtitle mb-2 text-muted">{todo.symbol}</h5>
              <h6 className="card-subtitle mb-2 text-muted">
              { todo.completed}
              { !todo.completed &&
                <span>
                  Pending
                </span>
              }              
              </h6>
            </div>
          </div>
        ))}
        </div>
       </div>
    );
  }
}
      
